import React, { useState } from 'react';
import styled from 'styled-components';

import picovnik from './assets/picovnik';

const Container = styled.div`
  height: 100%;
  background: #7dc0b8;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

Container.Buttons = styled.div`
  width: 100%;
  padding: 1.5em 0;
  display: flex;
  justify-content: space-between;
`;

const PageButton = styled.button`
  border-radius: none;
  outline: none;
  border: none;
  font-size: 36px;
  color: #ffffff;
  background: #79b3ae;
  padding: 0 20px 8px 20px;
`;

const Image = styled.img`
  max-height: 80vh;
  max-width: 50em;

  @media only screen and (max-width: 768px) {
    max-width: 85vw;
  }
`;

const Text = styled.span`
  padding-top: 1.5em;
  font-size: ${props => props.size || 1}em;
  font-family: 'Muli', sans-serif;
  font-weight: 200;
  color: #ffffff;
  margin: .2em 0;
`;

const cycle = (val, mod) => {
  var remain = val % mod;

  return Math.floor(remain >= 0 ? remain : remain + mod);
};

const Picovnik = () => {
  const [pageNumber, setPageNumber] = useState(0);
  const goForward = () => setPageNumber(cycle((pageNumber + 1) , 8))
  const goBackward = () => setPageNumber(cycle((pageNumber - 1) , 8))

  return (
    <Container>
      <Text size={2}>Picovnik</Text>

      <div>
        <Container.Buttons>
          <PageButton onClick={goBackward}>↜</PageButton>

          <PageButton onClick={goForward}>↝</PageButton>
        </Container.Buttons>

        <Image src={picovnik[pageNumber]} />
      </div>
    </Container>
  );
}

export default Picovnik;
